import React from 'react';
import { FC  } from 'react';
import './Input.css'

export interface MyInputProps {
    block?: boolean,
    placeholder?: string,
    label?: string,
    onChange?: () => void,
}
const Input: FC<MyInputProps> =
    ({
         ...props
     }) => {
        return (
            <label className="my-input">
                {props.label}
                <input type="text" {...props}/>
            </label>

        )
    }

    export default Input;
